        <p>Of the four possible prompts, our team picked:</p>
        <h3 class="header-padding">
Hidden Figures</h3>
<h4>
Design a way to break down the physical, organizational or attitudinal barriers that people with hidden disabilities or impairments can face in society, to enable them to live their lives to the full. </h4>
   <h2 class="header-padding"><a class="font-deco"> Our team spent a month and a half on understanding Autism Spectrum Disorder.</a></h2>
      </b-container>
