            <h4><span class="heading5">Research</span></h4>
<p>We split the research into different categories, and fields. I was tasked with contacting organizations, people, and interviewing them. Over the next six weeks, I was able to contact and interview 5 organizations, and 5 parents of children with ASD. I thought that it was important to read some journals about the disorder and compiled a folder of 30 papers that I delved into to try and understand how ASD affects people. </p>
    </b-col>
    <b-col cols="12" sm="12" md="12" lg="6" xl="6">
               <b-img rounded class="image-size" :src="research_page" img-alt="This is a picture of some of the research we have completed for our project."></b-img>
    </b-col>
    </b-row>
    <b-row>
      <b-col class="list" cols="12" sm="12" md="12" lg="6" xl="6">
    <h4><span class="heading5">Autism Spectrum Disorder</span></h4>
<ul>
  <li>Saying “Autism” is generally not accepted instead we should use ASD</li>
  <li>1 in 66 people in Canada has ASD and the number is growing</li>
  <li>Every person with ASD is different and it affects them differently</li>
  <li>There is a distinction between a shutdown and an overload</li>
  <li>Women with ASD are often diagnosed later in life than men</li>
  <li>ASD is often separated into three categories of Mild, Moderate, and Severe - however, it can be hard to tell when a person fits into a category.</li>
  <li>The ASD market is very underrepresented and catered to</li>
  <li>“The Cliff” is a term used by the ASD community for when people turn 21 (in Canada) and all of the services the relied upon are taken away. </li>
</ul>
 </b-col>
       <b-col class="list" cols="12" sm="12" md="12" lg="6" xl="6">
    <h4><span class="heading5">People</span></h4>
<ul>
  <li>Scheduling for some people with ASD is very important</li>
  <li>85% of people with ASD who are college graduates don’t have a job</li>
  <li>Some people with ASD are rarely supported by their managers</li>
  <li>Some people with ASD can require extra support from managers at work sometimes up to 5 hours a day</li>
  <li>People with ASD can be great employees when they are given clear direction with tasks</li>
  <li>Some people with ASD can react poorly to different colors, foods, noises, loudness, and sensations</li>
  <li>Relationships can be very difficult for some people with ASD</li>
  <li>Eye contact can be difficult for some people with ASD</li>
  <li>Some people with ASD perceive the social world as having rules but not knowing what they are</li>
</ul>
 </b-col>
</b-row>
